<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                注册
            </p>
        </section>
        <div class="con-con">
            <div class="list">
                <img src="../../assets/login/registered_icon_theusertypes@2x.png" slot="left" alt="">
                <md-input-item
                        title="用户类型"
                        ref="input11"
                        v-model="userType1"
                        placeholder="请选择用户框架"
                        @click.native="onEducShow"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_phone@2x.png" slot="left" alt="">
                <md-input-item
                        title="邮箱用户名"
                        ref="input11"
                        :clearable="true"
                        placeholder="请输入邮箱用户名"
                        v-model="userInfo.username"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_code@2x.png" slot="left" alt="">
                <md-input-item
                        title="密码"
                        ref="input11"
                        type="password"
                        v-model="userInfo.password"
                        :clearable="true"
                        placeholder="请设置密码"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_code@2x.png" slot="left" alt="">
                <md-input-item
                        title="确认密码"
                        ref="input11"
                        type="password"
                        v-model="againPassword"
                        :clearable="true"
                        placeholder="请确认密码"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_Invitecode@2x.png" slot="left" alt="">
                <md-input-item
                        title="邀请码"
                        ref="input11"
                        :clearable="true"
                        placeholder="请输入邀请码"
                >
                </md-input-item>
            </div>
            <div class="list">
                <img src="../../assets/login/registered_icon_verificationcode@2x.png" slot="left" alt="">
                <md-input-item
                        title="验证码"
                        ref="input11"
                        v-model="userInfo.code"
                        :clearable="true"
                        placeholder="请输入验证码"
                >
                    <div v-if="!isSendShow" class="input-operator" slot="right" @click="sendCode">获取验证码</div>
                    <div v-if="isSendShow" class="min" slot="right">{{ num }}秒</div>
                </md-input-item>
            </div>
        </div>
        <md-button type="default" @click="onRegister">立即注册</md-button>
        <md-picker
                ref="typePicker"
                v-model="isEducShow"
                :data="userType"
                @confirm="onEducConfirm"
                title="选择操作类型"
        ></md-picker>
    </main>
</template>
<script>
import { Field, CellItem, InputItem, Toast, Icon, Picker, Button } from 'mand-mobile'
import { userType } from '../../utils/select'
import { sendCode, register } from '../../apiUrl/login'
import qs from 'qs'

export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [Button.name]: Button,
    [Picker.name]: Picker,
    [InputItem.name]: InputItem,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon
  },
  data () {
    return {
      isEducShow: false,
      userType: userType,
      userType1: '',
      num: 60,
      isSendShow: false,
      againPassword: '',
      iphone: '',
      userInfo: {
        roleId: '',
        username: '',
        password: '',
        code: ''
      }
    }
  },
  methods: {
    sendCode () {
      console.log(this.userInfo.username)
      if ((/^1[3456789]\d{9}$/.test(this.userInfo.username))) {
        const args = {
            username: this.userInfo.username
        }
        sendCode(args).then((res) => {
          if (res.statusMsg === 'success') {
            Toast.succeed('短信已发送！')
          }
        })
        this.isSendShow = true
        const val = setInterval(() => {
          this.num--
          if (this.num === 0) {
            clearTimeout(val)
            this.isSendShow = false
            this.num = 60
          }
        }, 1000)
      } else {
        Toast.failed('手机号格式不正确')
      }
    },
    onRegister () {
      if (!this.userInfo.username) {
        Toast.failed('请输入手机号')
        return
      }
      if (!this.userInfo.password) {
        Toast.failed('请输入密码')
        return
      }
      if (!this.againPassword) {
        Toast.failed('请输入确认密码')
        return
      }
      if (!this.userInfo.code) {
        Toast.failed('请输入验证码')
        return
      }
      if (this.userInfo.password === this.againPassword) {
        register(this.userInfo).then((res) => {
          console.log(res.message)
          if (!res.message) {
            Toast.succeed('注册成功！')
            this.$router.push('/login')
          } else {
            Toast.failed(res.message)
          }
        }).catch((e) => {
          Toast.failed(e)
        })
      } else {
        Toast.failed('两次密码输入不一致')
      }
    },
    onEducShow () {
      document.activeElement.blur()
      this.isEducShow = true
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    // 类型选择确认
    onEducConfirm (columnsValue) {
      console.log(`[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(columnsValue)}`)
      // this.Education = columnsValue[0].text
      this.userType1 = columnsValue[0].text
      this.userInfo.roleId = columnsValue[0].value
    }

  }
}
</script>

<style lang="stylus" scoped>
    .input-operator
        width 150px
        height 50px
        color #fff
        font-size 18px!important
        line-height 50px
        margin-right 20px
        background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1));
        border-radius:4px;
    .min
        width 150px
        height 50px
        color #999
        background #E5E8EE
        font-size 18px!important
        line-height 50px
        margin-right 20px
    .con-con
        width 90%
        margin 0px auto
        margin-top 150px
        background:rgba(255,255,255,1);
        box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
        border-radius:12px;
        .list
            img
                width 28px
                height 30px
                float left
                margin-left 32px
                margin-top 35px
    /deep/.md-cell-item-body
        justify-content space-between!important
        padding 0px 20px
        text-align left
    /deep/.md-cell-item-content
        flex: 1 1 0%!important
    /deep/.md-cell-item-left
        margin-right 10px
    /deep/.md-cell-item
        height 91px
    /deep/.md-cell-item-body:before
        top:91px
        bottom auto
    /deep/.md-button
        width:660px;
        height:88px;
        background:rgba(2,25,85,1);
        border-radius:12px;
        margin 0 auto
        color #fff
        margin-top 60px
    /deep/.md-field-item-title
        text-indent 10px
        text-align left
</style>
